<template>
  <div class="interaction">
    <el-main>
      <el-table
        :data="tableData"
        stripe
        style="width: 100%"
        @selection-change="selectionChange"
      >
        <el-table-column
          prop="select"
          label=""
          width="50"
          show-overflow-tooltip
          type="selection"
        >
        </el-table-column>
        <el-table-column
          prop=""
          label="#"
          width="100"
          type="index"
          :index="formatter"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="userName"
          label="用户名"
          width="150"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="content"
          label="内容"
          width="180"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop=""
          label="跟帖"
          width="150"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="userAvatar"
          label="头像"
          width="180"
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <img :src="scope.row.userAvatar" alt="" class="imgItem">
          </template>
        </el-table-column>
        <el-table-column
          prop="createAt"
          label="时间"
          width="180"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              @click="del(scope.row._id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
    <el-footer>
      <el-button type="primary" @click="toggleSelection(multiSelection)">批量删除</el-button>
      <el-pagination
        background
        layout="prev, pager, next,jumper"
        :total="total"
        :current-page="page"
        :page-size="pageSize"
        @current-change="pageChange">
      </el-pagination>
    </el-footer>
  </div>
</template>
<script>
    export default{
      name: "",
      data(){
          return{
            tableData:[],
            //选择器
            multiSelection:[],
            userId:[],
            //分页器
            pageSize:5,
            page:1,
            total:10
          }
      },
      methods:{
        //获取用户列表
        getMsg(){
          this.$axios.get('getMsg').then( res => {
            this.tableData = res.data
          })
        },
        //删除
        del(id){
          this.$confirm('此操作将永久删除该内容, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$axios.post('delMsg', {id}).then(res => {
              if(res.code == 200){
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
                setTimeout( () => {
                  this.$router.go(0)
                },1000)
              }else{
                this.$message({
                  type: 'error',
                  message: '删除失败!'
                });
              }
            })
          }).catch(() => {
            this.$message({
              type: 'warning',
              message: '已取消删除'
            });
          })
        },
        //批量选择
        selectionChange(val){
          this.multiSelection = val
        },
        toggleSelection(rows){
          if(rows){
            for(var key in rows){
//                console.log(key)
//                this.userId = rows[key]._id
              this.userId.push(rows[key]._id)
            }
            this.$axios.post('delManyMsg', {id:this.userId}).then( res => {
              if(res.code == 200) {
                this.$message({
                  type:'success',
                  message:'批量删除成功'
                })
              }
            })
          }else {
            this.$message({
              type:'warning',
              message:'没有选择'
            })
          }
        },
        //获取数据库文档总数
        getNumber(){
          this.$axios.get('getInteractionNum').then( res => {
            this.total = res.data
          })
        },
        //底部分页器
        //这个时候利用formatter就可以实现索引累加啦
        formatter(index){
          //放回索引
          return index + (this.page -1) *this.pageSize +1
        },
        pageChange(val){
//          console.log(`当前页: ${val}`);
          this.page = val
          this.$axios.get('getMsg',{page:val, pageSize:this.pageSize}).then( res => {
            this.tableData = res.data
          })
        },
      },
      created(){
          this.getMsg();
          this.getNumber()
      }
    }
</script>
<style scope>
  .interaction {
    width: 100%;
    padding-top: 2px;
    padding-left: 20px;
  }
  /*内容区*/
  .interaction .el-main{
    margin:10px 22px 10px 0;
    padding: 0 14px;
    border:1px solid #dfe7ed;
  }
  .interaction .el-table .has-gutter .cell{
    color:#000;
  }
  /*头像图片大小*/
  .imgItem{
    width: 50px;
    height: 50px;
  }
  /*底部*/
  .interaction .el-footer{
    /*position: fixed;*/
    /*min-height: 500px;*/
    /*bottom: 20px;*/
    /*right:20px;*/
    position:relative;
    height: 56px !important;
    margin:0 22px 0 0;
    padding:0;
    background-color: #f1f1f1 !important;
  }
  .interaction .el-button{
    margin:10px 0 0 10px;
  }
  .interaction .el-footer .el-pagination{
    display: inline-block;
    position:absolute;
    top:12px;
    right:15px;
  }
  .interaction .el-footer .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
    background-color: #ffffff;
  }
</style>



